.section {
  width: 250px;
  border: 1px solid #494848;
  border-radius: 5px;
  display: inline-block;
}

.avatar {
  border-radius: 50%;
  margin: 20px auto;
}


.tag,
.location {
  color: rgb(138, 134, 134);
}

.stats {
  display: flex;
  background-color: #acacb8;
  margin: 0;
  overflow: hidden;
}

.item {
  text-align: center;
  display: grid;
  padding: 10px;
  width: 85px;
  border: 1px solid #a7a5a5;
  border-radius: 2px;
  height: 80px;
}

.label {
  margin-top: 10px;
  color: grey;
}

.quantity {
  font-size: 20px;
  font-weight: bold;
}
